import React from 'react';
import { FaHtml5 } from 'react-icons/fa';
import { FaCss3 } from 'react-icons/fa';
import { FaReact } from 'react-icons/fa';
import { FaPhp } from 'react-icons/fa';
import { FaNodeJs } from 'react-icons/fa';
import { FaJs } from 'react-icons/fa';
import './style.css';
import htmlsvg from './../../assets/svg/html5.svg';

const tecnologyList = [
	{
		name: 'HTML',
		icon:<FaHtml5 />,
		leavel: 'avançado',
		experience: '2 anos',
		numberProject: 25,
	},
	{
		name: 'CSS',
		icon:<FaCss3 />,
		leavel: 'avançado',
		experience: '2 anos',
		numberProject: 25,
	},
	{
		name: 'JavaScript',
		icon:<FaJs />,
		leavel: 'intermédio',
		experience: '2 anos',
		numberProject: 10,
	},
	{
		name: 'PHP',
		icon:<FaPhp />,
		leavel: 'intermédio',
		experience: '2 anos',
		numberProject: 10,
	},
	{
		name: 'NODE JS',
		icon:<FaNodeJs />,
		leavel: 'intermédio',
		experience: '2 anos',
		numberProject: 10,
	},
	{
		name: 'React JS',
		icon:<FaReact />,
		leavel: 'intermédio',
		experience: '2 anos',
		numberProject: 10,
	},
]


const tecnology = () => {
	return(
		<>
			<section className="tecnology pdg">
				<div className="container tecnology-container">
					<header>
						<h1>
							<span className="jogo-da-velha">#</span>
							TECNOLOGIAS
						</h1>
					</header>
					{tecnologyList.map(item=>{
						return(
							<article className="tecnology-card">
								<div className="tecnology-card-up">
									<div className="tecnology-card-up-img">
										{/*<img src="#" alt={item.name} />*/}
										{item.icon}
									</div>
								</div>

								<div className="tecnology-card-down">
									<table>
										<tr>
											<td>Nivel</td>
											<td className="optionright">{item.leavel}</td>
										</tr>

										<tr>
											<td>Experiências</td>
											<td className="optionright">{item.experience}</td>
										</tr>

										<tr>
											<td>N.º de projectos</td>
											<td className="optionright">{item.numberProject}</td>
										</tr>
									</table>

									<button>Ver projectos</button>
								</div>

							</article>

						);
					})}
					{function(){return('<h1>Ola</h1>')}}
				</div>
			</section>
		</>
	);
}

export default tecnology;