import React from 'react';
import './style.css';
import { FaLink } from 'react-icons/fa'
import img_ecoleta from './../../assets/img/ecoleta.png';

const Project = () => {

	const Projects = [
		{
			imgSrc: img_ecoleta,
			year: 2020,
			description: 'Calculadora',
		},
		{
			imgSrc: img_ecoleta,
			year: 2017,
			description: 'E-commerce',
		},
		{
			imgSrc: img_ecoleta,
			year: 2013,
			description: 'Partilha de Vídeos',
		},
		{
			imgSrc: img_ecoleta,
			year: 2013,
			description: 'Partilha de Vídeos',
		},
		{
			imgSrc: img_ecoleta,
			year: 2013,
			description: 'Partilha de Vídeos',
		},
		{
			imgSrc: img_ecoleta,
			year: 2013,
			description: 'Partilha de Vídeos',
		},
	]



	return(
		<>
			<section className="project pdg">
				<div className="container project-container">
					<header className="project-header">	
						<h1>
							<span className="jogo-da-velha">#</span>
							PROJECTOS
						</h1>
					</header>

					<div id="meucarrossel" className="box-project">

						{
							Projects.map(element=>{
								
								return(
								<article className="project-card">
									<div className="project-card-img">
										<img src={element.imgSrc} alt={element.description} />
										<div  className="project-card-link">
											<a href="/"><FaLink /></a>
										</div>
									</div>


									<footer className="project-card-footer">
										<span className="project-card-footer-name">{element.description}</span>
										<span className="project-card-footer-year">{element.year}</span>
									</footer>
								</article>
								)
							})
						}
					</div>
				</div>
			</section>
		</>
	);
}

export default Project;