import React from 'react';
import './style.css';
import photo from './../../assets/img/domingosphoto.png';

const About = () => {
	return(
		<>
			<article className="about pdg">
				<div className="container about-container">
					<header className="about-header">	
						<h1>
							<span className="jogo-da-velha">#</span>
							SOBRE MIM
						</h1>
					</header>

					<div className="about-text">
						Chamo-me Domingos Nkula Pedro, sou formado em Gestão de Recursos Humanos no Instituto Médio Comercial de Luanda! Mas ingressei para a área de TI (Técnologia de Informação) em 2020, ano em que ocorreu a pandemia da COVID-19 no mundo todo. Por influência de amigos e por uma paixão que já carregava por computadores, decidi estudar algo que via tanto em filmes e séries, mas que não sabia como funcionava -  a Programação. Mas longo que mergulhei em vídeos e documentos que tratavam acerca de programação, comecei a enxegar um mundo fantástico, onde a lógica e criatividade falam mais alto.
					</div>

					<div className="about-img">
						<img src={photo} alt="Domingos Pedro" />
					</div>
				</div>
			</article>
		</>
	);
}

export default About;
